import { CSSResult, CSSResultArray, LitElement } from 'lit';
import { registerElement } from './register';
import { unsafeStatic } from 'lit/static-html.js';

export type GenerateStyles = (tagName: string) => CSSResult | CSSResult[] | CSSResultArray | undefined;

/**
 * Decorator attributes for defineElement
 *
 * @param sub @deprecated
 */
interface ElementDecoratorAttr {
    tagName: string;
    styles?: GenerateStyles;
    version: string;
    sub?: any[];
}

export function defineElement(attr: ElementDecoratorAttr) {
    return function _element<T extends new (...args: any[]) => {}>(constr: T) {
        return class extends constr {
            static readonly tagName = attr.tagName;
            static readonly generateStyles = attr.styles;
            static readonly version = attr.version;
            static readonly sub = attr.sub || [];
        };
    };
}

interface DynSub {
    module: () => Promise<any>;
    elem: string[] | string;
}
export abstract class BaseElement extends LitElement {
    // Static members
    static generateStyles: GenerateStyles;
    static readonly tagName: string;
    static readonly version: string;
    static readonly sub: any[] = []; /* @deprecated */
    static tag: string;

    static register(namespace: string): string {
        if (!this.tagName) {
            throw Error('Subclass must set tagName : ' + this.toString());
        }

        /* This _subRegister construct is deprecated */
        const _subRegister = (arr) => {
            arr.forEach((d) => {
                _subRegister(d.sub); // Stack overflow protection? No, should not be more than 3 level at the moment
                this.subRegister(d);
            });
        };
        _subRegister(this.sub);

        return registerElement(this, namespace);
    }

    protected static subRegister(elem) {
        if (!elem.tag) {
            elem.tag = unsafeStatic(registerElement(elem, ''));
        }
        return elem;
    }

    protected static dynSubRegister = (imports: DynSub[] | DynSub, callback: () => void) => {
        const imports2 = this._filterUndefinedImports(imports);

        if (imports2.length > 0) {
            Promise.all(imports2.map((a) => a.module()))
                .then((modules) => {
                    modules.forEach((module, index) => {
                        (imports2[index].elem as string[]).forEach((e) => {
                            const resource = module[e] ?? module.default;
                            if (resource) {
                                if (resource.tagName) {
                                    BaseElement[e] = this.subRegister(resource);
                                } else {
                                    BaseElement[e] = resource;
                                }
                            } /* could throw error here... */
                        });
                    });
                    callback();
                })
                .catch((e) => {
                    window['console']['error']('error', e);
                    callback();
                });
        } else {
            callback();
        }
    };

    private static _filterUndefinedImports(imports: DynSub[] | DynSub) {
        const imports2: DynSub[] = [];
        (Array.isArray(imports) ? imports : [imports]).forEach((imp) => {
            const elem: string[] = [];
            const elemArray = Array.isArray(imp.elem) ? imp.elem : [imp.elem];
            elemArray.forEach((e) => {
                if (BaseElement[e] === undefined) {
                    elem.push(e);
                }
            });
            if (elem.length > 0) {
                imports2.push({ module: imp.module, elem });
            }
        });
        return imports2;
    }
}
