import { CSSResult, unsafeCSS } from '@lit/reactive-element';

/**
 * Create selector helper functions for children and modifiers.
 * @param baseClassName - The element's base className.
 * @param rootClassName - The root element's className.
 */
export const cssSelector = (baseClassName: string, rootClassName = 'root') => {
    baseClassName = baseClassName.trim();
    const host = unsafeCSS(`.${baseClassName}`);
    const root = unsafeCSS(`.${baseClassName}-${rootClassName}`);

    function child(...classNames: string[]): CSSResult {
        if (!classNames || !classNames[0]) return unsafeCSS(`.${baseClassName}-`);
        const all = classNames
            .map((className) => {
                className = className.trim();
                return `.${baseClassName}-${className}`;
            })
            .join(' ');
        return unsafeCSS(all);
    }
    function is(...modifiers: string[]): CSSResult {
        if (!modifiers[0]) return unsafeCSS('');

        const modifierIsTypeNot = (modifier: string) => modifier[0] === '!';
        const getModifierSelector = (modifier: string) => {
            const isTypeNot = modifierIsTypeNot(modifier);
            if (isTypeNot) {
                modifier = modifier.substring(1);
                return `:not(.${baseClassName}--${modifier})`;
            }
            return `.${baseClassName}--${modifier}`;
        };

        return unsafeCSS(
            modifiers.reduce(
                (result, modifier) => {
                    modifier = modifier.trim();
                    return result + getModifierSelector(modifier);
                },
                modifierIsTypeNot(modifiers[0][0]) ? root : ''
            )
        );
    }

    function groupedIs(modifier: string, ...classes: string[]): CSSResult {
        return unsafeCSS(classes.map((s) => `${is(modifier)} ${child(s)}`).join(','));
    }

    return { host, root, child, is, groupedIs };
};
