import { css, cssSelector, GenerateStyles, style, unsafeCSS } from '@sartorius-elements/base';

// see https://www.sartorius.com/brand-platform/applications/digital

export const buttonStyles: GenerateStyles = (tagName) => {
    const { root, child, is } = cssSelector(tagName);

    return [
        style,
        css`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            :host {
                display: inline-block;
            }
            :host([full-width]) {
                width: 100%;
            }
            :host([full-height]) {
                height: 100%;
            }

            ${root} {
                border: 1px solid transparent;
                border-radius: 0;
                cursor: var(--button-cursor, pointer);
                font-size: var(--font-size-02);
                height: var(--button-height, 40px);
                outline: 0;
                appearance: auto;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
                position: relative;
                text-align: center;
                text-decoration: none;
                user-select: none;
                width: var(--button-width, auto);
                min-width: var(--button-min-width);
                vertical-align: top;
                line-height: normal;
                padding-left: var(--button-padding, 23px);
                padding-right: var(--button-padding, 23px);
                margin: 0;
                font-weight: var(--font-weight-04);
                --expand-duration: 0.2s;
            }
            ${is('!compact', '!icon-after')} ${child('loader-icon')} {
                padding: 0px 2px;
                margin-right: 8px;
            }

            ${is('!compact', 'icon-after')} ${child('loader-icon')} {
                margin-left: 8px;
                padding: 0px 2px;
            }

            ${child('loader')} {
                --loader-display: flex;
                --loader-align: center;
                --loader-justify: center;
                --loader-slot-display: block;
                --loader-content-gap: 0px;
            }

            ${child('combobox')} {
                width: 40px;
                height: 100%;
                background: blue;
            }

            /* Compact styles */

            ${is('compact')} {
                width: 40px;
                padding: 0 !important;
                font-size: 1.5rem;
            }
            ${is('compact', 'table-move')} {
                width: 24px;
                height: 100%;
            }
            ${is('compact', 'small')} {
                font-size: 1.5rem;
                width: 36px;
            }
            ${is('compact', 'large', '!notification')} {
                font-size: 1.5rem;
                width: 48px;
            }
            ${is('compact', 'extra-small')} {
                width: 18px;
            }
            ${is('compact', 'inline-small')} {
                width: 24px;
            }

            /* Sized above will be removed soon */

            ${is('compact', 'xlarge')} {
                width: 56px;
            }
            ${is('compact', 'large')} {
                width: 48px;
            }
            ${is('compact', 'medium')} {
                width: 40px;
            }
            ${is('compact', 'small')} {
                width: 32px;
            }
            ${is('compact', 'xsmall')} {
                width: 24px;
            }

            ${is('compact')} ${child('label')} {
                display: none;
            }

            ${child('label')} {
                display: flex;
                align-items: center;
            }
            /* Sizes */
            ${is('inline-small')} {
                height: var(--button-height, 24px);
            }
            ${is('small')} {
                height: var(--button-height, 36px);
            }
            ${is('extra-small')} {
                height: var(--button-height, 18px);
                padding: 0;
            }
            ${is('inline-button')} {
                height: 24px;
                padding: 0 12px;
                background: var(--color-neutral-gray-25) !important;
                color: var(--color-text-default);
            }
            /* Sizes above will be removed soon */

            ${is('xlarge')} {
                font-size: var(--font-size-03);
                height: var(--button-height, 56px);
                padding-left: var(--button-padding, 32px);
                padding-right: var(--button-padding, 32px);
            }
            ${is('large')} {
                height: var(--button-height, 48px);
                padding-left: var(--button-padding, 27px);
                padding-right: var(--button-padding, 27px);
            }
            ${is('medium')} {
                height: var(--button-height, 40px);
                padding-left: var(--button-padding, 23px);
                padding-right: var(--button-padding, 23px);
            }
            ${is('small')} {
                height: var(--button-height, 32px);
                padding-left: var(--button-padding, 19px);
                padding-right: var(--button-padding, 19px);
            }
            ${is('xsmall')} {
                font-size: var(--font-size-01);
                height: var(--button-height, 24px);
                padding-left: var(--button-padding, 8px);
                padding-right: var(--button-padding, 8px);
            }

            /* Slot styles */

            ${is('table-move', 'compact', '!icon-after')} ::slotted([slot='icon-slot']) {
                font-size: var(--font-size, var(--font-size-01));
            }

            ${is('xlarge', 'compact', '!icon-after')} ::slotted([slot='icon-slot']) {
                font-size: var(--icon-size, 28px);
            }
            ${is('table-move', 'compact', 'icon-after')} ::slotted([slot='icon-slot']) {
                font-size: var(--icon-size, var(--icon-size-01));
            }
            ${is('large', 'compact', '!icon-after')} ::slotted([slot='icon-slot']) {
                font-size: var(--icon-size, 24px);
            }
            ${is('medium', 'compact', '!icon-after')} ::slotted([slot='icon-slot']) {
                font-size: var(--icon-size, 24px);
            }
            ${is('small', 'compact', '!icon-after')} ::slotted([slot='icon-slot']) {
                font-size: var(--icon-size, 20px);
            }
            ${is('xsmall', 'compact', '!icon-after')} ::slotted([slot='icon-slot']) {
                font-size: var(--icon-size, 20px);
            }
            ${is('!compact', 'xsmall', '!icon-after')} ::slotted([slot='icon-slot']) {
                margin-right: 2px;
                font-size: var(--icon-size, 20px);
            }
            ${is('!compact', 'small', '!icon-after')} ::slotted([slot='icon-slot']) {
                margin-right: 2px;
                font-size: var(--icon-size, 20px);
            }
            ${is('!compact', 'medium', '!icon-after')} ::slotted([slot='icon-slot']) {
                margin-right: 4px;
                font-size: var(--icon-size, 24px);
            }
            ${is('!compact', 'large', '!icon-after')} ::slotted([slot='icon-slot']) {
                margin-right: 4px;
                font-size: var(--icon-size, 24px);
            }
            ${is('!compact', 'xlarge', '!icon-after')} ::slotted([slot='icon-slot']) {
                margin-right: 8px;
                font-size: var(--icon-size, 28px);
            }

            ${is('!compact', 'xsmall', 'icon-after')} ::slotted([slot='icon-slot']) {
                margin-left: 2px;
                font-size: var(--icon-size, 20px);
            }
            ${is('!compact', 'small', 'icon-after')} ::slotted([slot='icon-slot']) {
                margin-left: 2px;
                font-size: var(--icon-size, 20px);
            }
            ${is('!compact', 'medium', 'icon-after')} ::slotted([slot='icon-slot']) {
                margin-left: 4px;
                font-size: var(--icon-size, 24px);
            }
            ${is('!compact', 'large', 'icon-after')} ::slotted([slot='icon-slot']) {
                margin-left: 4px;
                font-size: var(--icon-size, 24px);
            }
            ${is('!compact', 'xlarge', 'icon-after')} ::slotted([slot='icon-slot']) {
                margin-left: 8px;
                font-size: var(--icon-size, 28px);
            }

            /* Default styles */

            ${is('primary', '!notification', '!disabled')} {
                background-color: var(--color-surface-action-primary);
                color: var(--color-text-default);
            }
            ${is('high-severity', '!notification', '!disabled')} {
                background-color: var(--color-surface-support-danger);
                color: var(--color-text-default-inverted);
            }
            ${is('secondary', '!notification', '!disabled')} {
                background-color: var(--color-surface-action-secondary);
                color: var(--color-text-default);
            }
            ${is('tertiary', '!notification', '!disabled')} {
                background-color: var(--button-color, transparent);
                color: var(--color-text-default);
            }
            ${is('tertiary-on-black', '!disabled')} {
                font-weight: 700;
                background-color: var(--button-color, transparent);
                color: var(--color-text-default-inverted);
            }

            ${child('inner')} {
                display: flex;
                align-items: center;
                justify-content: center;
            }

            ${child('circle')} {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 32px;
                height: 32px;
                border: 2px solid var(--color-primary-2, var(--color-neutral-white));
                border-radius: 50%;
                font-weight: bold;
                margin-right: 8px;
            }

            ${is('primary')} ${child('circle')} {
                border: 2px solid var(--color-primary-3, var(--color-neutral-black));
            }

            ${is('full-width')} {
                width: 100%;
            }
            ${is('full-height')} {
                height: 100%;
            }

            /* Disabled styles */

            ${is('primary', '!notification', 'disabled')} {
                color: var(--color-text-disabled-transparent);
                background: var(--color-surface-action-primary);
                pointer-events: none;
            }
            ${is('secondary', '!notification', 'disabled')} {
                color: var(--color-text-disabled-transparent);
                background: var(--color-surface-action-disabled-full);
                pointer-events: none;
            }
            ${is('tertiary', '!notification', 'disabled')} {
                color: var(--button-text-color, var(--color-text-disabled-transparent));
                background: transparent;
                font-weight: 700;
                pointer-events: none;
            }
            ${is('tertiary-on-black', 'disabled')} {
                background: transparent;
                color: var(--button-text-color, var(--color-text-disabled-transparent));
                pointer-events: none;
            }
            ${is('high-severity', 'disabled')} {
                pointer-events: none;
                color: var(--button-text-color, var(--color-text-disabled-inverted));
                background: var(--color-surface-support-danger-active);
            }
            ${is('inline-button', 'disabled')} {
                color: var(--color-text-disabled-transparent) !important;
            }

            /* Hover states */

            ${is('primary', '!disabled')}:hover {
                transition: background-color var(--expand-duration) ease-in;
                background-color: var(--color-surface-action-primary-hover);
            }
            ${is('high-severity', '!disabled')}:hover {
                transition: background-color var(--expand-duration) ease-in;
                background-color: var(--color-surface-support-danger-hover);
            }
            ${is('secondary', '!disabled')}:hover {
                transition: background-color var(--expand-duration) ease-in;
                background-color: var(--color-surface-action-secondary-hover);
            }
            ${is('tertiary', '!disabled')}:hover {
                transition: background-color var(--expand-duration) ease-in;
                background-color: var(--button-hover-color, var(--color-surface-action-hover));
            }
            ${is('tertiary-on-black', '!disabled')}:hover {
                transition: background-color var(--expand-duration) ease-in;
                background-color: var(--button-hover-color, #ffffff26);
                color: var(--button-hover-text-color, var(--color-primary-2, var(--color-text-default-inverted)));
            }
            ${is('inline-button', '!disabled')}:hover {
                transition: background-color var(--expand-duration) ease-in;
                background-color: var(--button-hover-color, var(--color-surface-action-secondary-hover)) !important;
            }

            /* Active states */

            ${is('primary', '!notification', '!inline-button')}:active {
                background-color: var(--color-surface-action-primary-active) !important;
            }

            ${is('high-severity', '!notification', '!inline-button')}:active {
                background-color: var(--color-surface-support-danger-active) !important;
            }

            ${is('primary', 'active', '!notification', '!inline-button')} {
                background-color: var(--color-surface-action-primary-active) !important;
            }
            ${is('high-severity', 'active', '!notification', '!inline-button')} {
                background-color: var(--color-surface-support-danger-active) !important;
            }

            ${is('secondary', 'active', '!notification', '!inline-button')} {
                background-color: var(--color-surface-action-secondary-active) !important;
            }

            ${is('tertiary', 'active', '!notification', '!inline-button')} {
                background-color: var(--button-active-color, var(--color-surface-action-active)) !important;
            }

            ${is('tertiary-on-black', 'active', '!notification', '!inline-button')} {
                background-color: var(--button-active-color, #ffffff40) !important;
            }

            ${is('secondary', '!notification', '!inline-button')}:active {
                background-color: var(--color-surface-action-secondary-active) !important;
            }
            ${is('tertiary', '!notification', '!inline-button')}:active {
                background-color: var(--button-active-color, var(--color-surface-action-active)) !important;
            }
            ${is('tertiary-on-black', '!notification', '!inline-button')}:active {
                background-color: var(--button-active-color, #ffffff40) !important;
            }

            ${is('inline-button', 'primary')}:active {
                background-color: var(--color-surface-action-secondary-active) !important;
            }

            /* Focus states */

            ${root}:focus-visible:before {
                content: ' ';
                display: block;
                position: absolute;
                inset: 1px;
            }
            ${is('primary')}:focus-visible:before {
                border: 2px dashed var(--button-focus-color, var(--color-neutral-black));
            }
            ${is('high-severity')}:focus-visible:before {
                border: 2px dashed var(--button-focus-color, var(--color-neutral-white));
            }
            ${is('secondary')}:focus-visible:before {
                border: 2px dashed var(--button-focus-color, var(--color-neutral-white));
            }
            ${is('tertiary')}:focus-visible:before {
                border: 2px dashed var(--button-focus-color, var(--color-neutral-black));
            }
            ${is('tertiary-on-black')}:focus-visible:before {
                border: 2px dashed var(--button-focus-color, var(--color-neutral-white));
            }

            /* Notification styles (deprecated, will be moved to separate component) */
            ${is('notification', 'compact')} ${child('circle')} {
                margin-right: unset;
            }
            ${is('notification', 'compact')} {
                width: unset !important;
            }
            ${is('notification')} {
                font-weight: normal;
                width: 144px;
                padding-left: var(--button-padding, 12px);
                padding-right: var(--button-padding, 12px);
                font-size: 12px;
            }
            ${is('notification')} ${child('inner')} {
                justify-content: flex-start;
            }
            ${is('notification', 'primary', '!disabled')} {
                background-color: var(--button-color, var(--color-surface-action-primary));
                color: var(--button-text-color, var(--color-text-default));
            }
            ${is('notification', 'secondary', '!disabled')} {
                background-color: var(--button-color, var(--color-tertiary-signal-blue));
                color: var(--button-text-color, var(--color-text-default-inverted));
            }
            ${is('notification', 'tertiary', '!disabled')} {
                background-color: var(--button-color, var(--color-tertiary-red));
                color: var(--button-text-color, var(--color-text-default-inverted));
            }
            ${is('notification', '!unseen-notifications', '!disabled')} {
                background-color: var(--button-color, var(--color-neutral-black));
                color: var(--button-text-color, var(--color-text-default-inverted));
            }
            ${is('notification', '!unseen-notifications')} ${child('circle')} {
                border: unset;
                color: var(--color-text-default);
            }
            ${is('notification', 'primary', '!unseen-notifications')} ${child('circle')} {
                background-color: var(--button-color, var(--color-surface-action-primary));
            }
            ${is('notification', 'secondary', '!unseen-notifications')} ${child('circle')} {
                background-color: var(--button-color, var(--color-tertiary-signal-blue));
            }
            ${is('notification', 'tertiary', '!unseen-notifications')} ${child('circle')} {
                background-color: var(--button-color, var(--color-tertiary-red));
            }

            ${is('notification', '!unseen-notifications', '!disabled')}:hover {
                background-color: var(--color-neutral-black-transparent-900);
            }

            ${is('notification', 'primary', 'unseen-notifications', '!disabled')}:hover {
                background-color: var(--color-surface-action-primary-hover);
                color: var(--button-hover-text-color, var(--color-text-default));
            }
            ${is('notification', 'secondary', 'unseen-notifications', '!disabled')}:hover {
                background-color: var(--button-hover-color, var(--color-tertiary-turqoise));
                color: var(--button-hover-text-color, var(--color-text-default-inverted));
            }
            ${is('notification', 'tertiary', 'unseen-notifications', '!disabled')}:hover {
                background-color: var(--button-hover-color, var(--color-surface-support-danger-hover));
                color: var(--button-hover-text-color, var(--color-text-default-inverted));
            }

            ${is('icon-after', '!compact', '!notification')} ${child('inner')} {
                display: flex;
                flex-direction: row-reverse;
            }

            ${is('icon-after', '!compact', '!notification')} ::slotted([slot='icon-slot']) {
                margin-right: 0px;
                margin-left: 4px;
                font-size: var(--icon-size, 1.5rem);
            }

            ${is('full-width', '!compact', '!notification')} ${child('loader')} {
                width: unset;
            }

            @media not all and (min-resolution: 0.001dpcm) {
                @supports (-webkit-appearance: none) {
                    ${root}:focus:before {
                        content: ' ';
                        display: block;
                        position: absolute;
                        inset: 1px;
                    }
                    ${is('primary')}:focus:before {
                        border: 2px solid var(--color-neutral-black);
                    }
                    ${is('secondary')}:focus:before {
                        border: 2px solid var(--color-neutral-white);
                    }
                    ${is('tertiary')}:focus:before {
                        border: 2px solid var(--color-neutral-black);
                    }
                    ${is('tertiary-on-black')}:focus:before {
                        border: 2px solid var(--color-neutral-white);
                    }
                    ${is('high-severity')}:focus:before {
                        border: 2px solid var(--color-neutral-white);
                    }
                }
            }
        `,
        // Container is not parseable by babel-plugin-template-html-minifier
        unsafeCSS(`
           @container small (width < 768px) {
                ${is('responsive')} {
                    width: 40px;
                    padding: 0;
                    font-size: 1.5rem;
                }
                ${is('responsive', 'small')} {
                    font-size: 1.5rem;
                    width: 36px;
                }
                ${is('responsive', 'large', '!notification')} {
                    font-size: 1.5rem;
                    width: 48px;
                }
                ${is('responsive', 'extra-small')} {
                    width: 18px;
                    height: 18px;
                    font-size: 1rem;
                    font-weight: normal;
                    padding: 0;
                }
                ${is('responsive', 'inline-small')} {
                    width: 24px;
                    height: 24px;
                    font-size: 1rem !important;
                }
                ${is('responsive')} ::slotted([slot='icon-slot']) {
                    margin-right: 0px !important;
                    font-size: inherit !important;
                }
                ${is('responsive')} ${child('label')} {
                    display: none;
                }
                ${is('responsive')} ${child('loader-icon')} {
                    margin-right: 0px !important;
                }
            }        
        `),
    ];
};
