export const createClassName = (
    baseClassName: string,
    modifiers?: { [key: string]: boolean }
): [string, string, string] => {
    baseClassName = baseClassName.trim();
    let classes = '';

    if (modifiers) {
        classes = Object.keys(modifiers)
            .reduce((prevClassName: string, key: string) => {
                const value = modifiers[key];
                if (!value) return prevClassName;
                return `${prevClassName} ${baseClassName}--${key}`;
            }, classes)
            .trim();
    }

    return [baseClassName, `${baseClassName}-root${classes ? ' ' : ''}${classes}`, classes];
};
