import { toKebabCase } from './cases';

const _globalPrefix = 'elements';

let studio = window['studio'];
if (!studio) {
    studio = window['studio'] = {};
}
let sui = studio['sui'];
if (!sui) {
    sui = studio['sui'] = {};
}
let registry = sui['registry'];
if (!registry) {
    registry = sui['registry'] = new Map<string, { version: string }>();
}

if (!sui['dump']) {
    sui['dump'] = (silent = false) => {
        /* eslint-disable no-console */
        const d = console.info;
        /* eslint-enable no-console */
        let i = 1;

        function logDump(tag, comp) {
            d(`  ${i} : ${tag} ${comp.version}`);
            ++i;
        }

        if (!silent && registry) {
            d('components registry:');
            registry.forEach((val, key) => logDump(key, val));
        }
        return registry;
    };
}

function customElementsDefine(tag: string, element: any, styles) {
    window.customElements.define(
        tag,
        class extends element {
            static styles = styles;
            static version = element.version;
        } as any
    );
}

function customElementsGet(tag: string) {
    return window.customElements.get(tag);
}

function createTag(Element: any, prefix: string, version = false) {
    return (
        (prefix ? `${prefix.trim()}-` : '') +
        _globalPrefix +
        `-${toKebabCase(Element.tagName)}` +
        (version ? `-${Element.version.replace(/\./g, '-')}` : '')
    );
}

export const registerElement = (element: any, namespace: string, version = false) => {
    const tag = createTag(element, namespace, version);
    if (!customElementsGet(tag)) {
        const styles = typeof element.generateStyles === 'function' ? element.generateStyles(tag) : element.styles;
        customElementsDefine(tag, element, styles);
        if (registry) {
            registry.set(tag, { version: element.version });
        }
    }
    return tag;
};
